import Regular from 'regularjs';

import {PopupMenu} from '../../../src';

const menu = [
    {
        title: ''
    }, {
        title: ''
    }, {
        title: '', children: [
            {
                title: '1'
            },
            {
                title: '2',
                disabled: true
            },
            {
                title: '3'
            },
            {
                title: '4'
            }
        ]
    }, {
        title: '', disabled: true
    }
];


export const PopupMenuExample = Regular.extend({
    template: `
<div class="doc-wrap">
    <div class="doc-sidebar"><MarkdownDoc url="/src/components/popupMenu/popupMenu.md"></MarkdownDoc></div>     
    <div class="doc-main">
        <button on-click="{this.handleClick()}"></button>
        <ui-cdk-menu menu="{menu}"></ui-cdk-menu>
    </div>   
</div>
    `,
    config() {
        this.data.menu = menu;
    },
    handleClick() {
        PopupMenu.show({
            top: 100,
            left: 1736
        }, menu, {scrollHide: true}).then(clickMenu => {
            console.log(clickMenu);
        });
    }
});

