import { RegularT } from 'regularts';
import template from './input-cdk.example.html';

export class InputCdkExample extends RegularT {
    template = template;

    handleFocus($event) {
        this.handleInputEvent('focus ', $event);
    }

    handleBlur($event) {
        this.handleInputEvent('blur ', $event);
    }

    handleInput($event) {
        this.handleInputEvent('input ', $event);
    }

    handleKeydown($event) {
        this.handleInputEvent('keydown ', $event);
    }

    handleKeyup($event) {
        this.handleInputEvent('keyup ', $event);
    }

    handleInputEvent(type, {event, value}) {
        console.info(type, value, event);
    }
}

export const InputCdkExampleComponent = RegularT.extend(InputCdkExample);
